<template>
    <div class="numkybrd-box" v-if="isShow">
        <div class="limit-line">
            <div class="res-input" :class="{ 'error-val': !isValid }"> {{ totalStr }} </div>
        </div>
        <div class="hexkbrd-box" :id="id">
            <div :class="`line${i}`" v-for="(lines, i) in words" :key="i">
                <div v-for="(char, j) in lines" :key="j" @click.stop="clickBoard(char)" :class="['word', wordClass(char)]">
                    <span :class="{ 'green-font': char == 'Enter' }">{{ char }}</span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { messageApi } from '@/components/message/message'
import { bus } from '@/main.js'

export default {
    data() {
        return {
            words: [
                ['A', '7', '8', '9', 'AC'],
                ['B', '4', '5', '6', "⬅"],
                ['C', '1', '2', '3', '0'],
                ['D', 'E', 'F', "Enter"]
            ],
            totalStr: "",
            isShow: true,
            id: undefined,
            illegalWords: [],

        }
    },
    methods: {
        clickBoard(c) {
            // 点击禁用字符 无效操作
            if (this.isIllegal(c, this.illegalWords)) return
            if (c == "AC") {
                this.totalStr = ""
                this.clbck(this.totalStr)
                return
            }
            else if (c == "⬅") {
                if (this.totalStr == "") return
                this.totalStr = this.totalStr.slice(0, this.totalStr.length - 1)
                this.clbck(this.totalStr)
                return
            }
            else if (c == "Enter") {
                if (this.totalStr.length < 4) {
                    let msg = this.$t('alarm.overFour')
                    messageApi.warning([msg])
                    this.clbck(this.totalStr)
                }
                else {
                    this.onEnter && this.onEnter(this.totalStr)
                    this.isShow = false
                }
                return
            }
            this.totalStr += c
            // 接收一个回调函数
            this.clbck(this.totalStr)
            // this.oldStr=this.totalStr

        },
        wordClass(c) {
            if (c == "AC") {
                return "AC";
            }
            if (c == "Enter") {
                return "enter";
            }

            if (c == "#+=" || c == ".123") {
                return "abc";
            }
            if (c == "CapsLk") {
                return "alt";
            }
            return this.isIllegal(c, this.illegalWords) ? 'illega' : ''
        },
        initData() {
            this.totalStr = `${this.oldStr}`
            this.id = 'hexbaord' + Math.random().toFixed(3) * 10000
        },
        initIllegaLWords() {
            this.illegalWords = this.imprprChrctr
        },
        // closeboard(e) {
        //     let isout = document.getElementsByClassName("numkybrd-box")[0].contains(e.target);
        //     if (!isout) {
        //         window.removeEventListener('click', this.closeboard)
        //         this.isShow = false
        //         this.onReject && this.onReject()
        //     }
        // },
        // 是否为无效字符
        isIllegal(word, words) {
            for (let i = 0; i < words.length; i++) {
                if (word == words[i]) {
                    return true
                }
            }
            return false
        }

    },
    props: {
        width: {
            type: String,
            default: '45rem'
        },
        height: {
            type: String,
            default: '20rem'
        },
        oldStr: {
            type: null,
            default: ""
        },
        top: {
            type: String,
            default: "10px"
        },
        // 接收一个回调函数
        clbck: {
            type: Function,
            default: function (val) {
                (val);
            }
        },
        // 禁用的字符
        imprprChrctr: {
            type: Array,
            default: function () {
                return []
            }
        }

    },
    created() {
        this.initData()
        this.initIllegaLWords()
        function closeboard(e) {
            let numkybrdBox = document.getElementsByClassName("numkybrd-box")[0];
            if (numkybrdBox && !numkybrdBox.contains(e.target)) {
                window.removeEventListener('click', closeboard);
                this.isShow = false
                this.onReject && this.onReject()
            }
        }
        // 添加点击事件监听器
        window.addEventListener('click', closeboard);
    },
    watch: {
        oldStr() {
            this.initData()
        },
        imprprChrctr: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initIllegaLWords()
            }
        }
    },
    computed: {
        isValid() {
            return this.totalStr.length >= 4;
        },
        textColor() {
            return this.isValid ? '#C00c155' : "red";
        }

    },
    mounted() {
        setTimeout(() => {
            window.addEventListener('click', this.closeboard)
        }, 50);
        bus.on('closeHexNumkb', () => {
                window.removeEventListener('click', this.closeboard)
                this.isShow = false
                this.onEnter && this.onEnter(this.val)
            })
    },
}
</script>
<style scoped>
.arrow-min {
    float: left;
    margin-left: 1rem;
}

.numkybrd-box {
    position: absolute;
    top: v-bind(top);
    width: 37.5rem;
    left: 50%;
    transform: translateX(-50%);
    z-index: 60;
    background-color: var(--Cee);
    border-radius: 6px;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
    margin-bottom: 0.4rem;
}

.num-keyboard div {
    flex: 1;
    margin-bottom: 0.2rem;
    display: flex;
    position: relative;
}

.limit-line {
    height: 20%;
    width: 36rem;
    position: relative;
    margin-top: 0.5rem;
    padding: 0.5rem 0;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--fff);
    border-radius: 4px;
}

.res-input {
    position: relative;
    left: 0;
    right: 0;
    width: 100%;
    height: 3.6rem;
    font-size: 3rem;
    font-weight: 500;
    line-height: 3.6rem;
    letter-spacing: 2px;
    color: v-bind(textColor);
    text-align: right;
    border: none;
    background-color: var(--fff);
}

.hexkbrd-box {
    position: relative;
    top: 0.5rem;
    height: 23rem;
    display: flex;
    flex-direction: column;
    background-color: var(--Cee);
}

.line0 {
    margin-top: 5px;
}

.line0,
.line1,
.line2,
.line3,
.line4 {
    position: relative;
    flex: 1;
    margin-bottom: 5px;
    padding-left: 10px;
    padding-right: 5px;
    display: flex;
}



.word {
    float: right;
    margin-right: 5px;
    background-color: var(--fff);
    flex: 1;
    border-radius: 0.5rem;
    position: relative;
    cursor: pointer;
    font-family: var(--FROBOT);
    text-align: center;
    font-size: 2.5rem;
}

.bksp {
    flex: 1.5 !important;
}


.space {
    flex: 7.5 !important;
}

.abc {
    flex: 2.7 !important;
}

.alt {
    flex: 2.1 !important;
}

span {
    font-family: var(--FROBOT);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.green {
    background-color: var(--C00c155)
}

.white {
    color: gray;
}

.enter {
    flex: 2.03 !important;
}

.green-font {
    /* color: var(--C00c155); */
    color: var(--C00c155);
    display: inline-block;
    width: 288px;
    height: 102px;
    position: absolute;
    top: 84%;
    left: 50%;
    /* background-color: green; */
}

.illegal {
    color: var(--CB0) !important;
}

.word:active {
    background-color: var(--C00c155);
}

.green-font:active {
    color: var(--fff) !important;
}
</style>