<template>
    <div class="profiles-box">
        <div class="search-pf-box">
            <div class="search-pf-download" @click="exportFile">
                <IconTextSvgButton :module="$store.state.f_status.environment_is_node ? 'Node' : module" class="clear-his-btn" intervalSvgLeft="52%" intervalTextLeft="52%" intervalTextTop="75%"
                    icon="Download" :text="$t('record.download')" textColor="#fff" fontFamily="--SHSC-B" fontSize="1.25rem"
                    fontWeight="bold" bgcColor="transparent" width="3.5rem" height="3rem">
                </IconTextSvgButton>

            </div>
            <div class="search-pf-condition">
                <div :contenteditable="true" :readonly="true" class="message normal"> {{
                    currentCndtnTextShow
                }} </div>
            </div>
            <div class="search-pf-operate">
                <div style="display: flex;" @click="showFilterDlg">
                    <div class="select-size">
                        <IconTextSvgButton class="clear-his-btn" intervalSvgLeft="21%" intervalTextLeft="60%" icon="Filter"
                            :text="$t('record.select')" textColor="#fff" fontFamily="--SHSC-B" fontSize="1.25rem"
                            fontWeight="bold" bgcColor="transparent" width="7rem" height="3rem">
                        </IconTextSvgButton>
                    </div>
                </div>
                <div style="display: flex;" @click="updateHisTableData">
                    <IconTextSvgButton class="clear-his-btn" intervalSvgLeft="27%" intervalTextLeft="72%" icon="Sync"
                        :text="$t('record.refresh')" iconSize="2rem" textColor="#fff" fontFamily="--SHSC-B"
                        fontSize="1.25rem" fontWeight="bold" bgcColor="transparent" width="7rem" height="3rem">
                    </IconTextSvgButton>
                </div>
            </div>
        </div>
        <div class="cndtns-array-box" v-show="isShowCndtnsArray">
            <div class="cndtns-array">
                <div v-for="(item, key) in conditionsArray" :key="key" :class="[
                    'cndtn-item',
                    'normal-item',
                    currentPointer == key ? 'active-cndtn' : '',
                ]" @click="updateCurrentCndtnPointer(key)">
                    {{ showCondtionText(item) }}
                </div>
            </div>
        </div>
        <div v-if="true" class="profiles-father-box">
            <div class="history-head-line">
                <span class="multistep"></span>
                <span>{{ $t("record.prcsid") }}</span>
                <span class="res-code">{{ $t("record.res_code") }}</span>
                <span>{{ $t("record.angle") }}/{{ sensorUnit("angle") }}</span>
                <span>{{ $t("record.torque") }}/{{ sensorUnit("torque") }}</span>
                <span class="time-span">{{ $t("record.time") }}</span>
            </div>
            <div v-for="(item, key) of getNumOfPage()" :key="key"
                :class="{ 'history-data-line': true, 'active-line': item == activeLine }" @click="throttleFn(item)">
                <span class="multistep" @click="isStepResults && item == activeLine ? multiStepResult() : null">
                    <svg-loader v-show="isStepResults && item == activeLine" icon="Vector"
                        :class="{ 'rotate': rotate }"></svg-loader>
                </span>
                <span>{{ getPset(item) }}</span>
                <span :class="statusClass(hisList[getRealIndex(item)].is_ok)" class="res-code">{{
                    getOk(item)
                }}</span>
                <span>{{ getPos(item) }}</span>
                <span>{{ getSensor(item) }}</span>
                <span class="time-span">{{ getTime(item) }}</span>
            </div>
        </div>
        <div v-if="true" class="profile-pages">
            <LeetxButton height="2.5rem" width="2rem" textColor="#3b3b3b" @click="clickHeadPage" class="page-btn head-btn"
                font-size="1.375rem" bgcColor="transparent" :text="$t('record.homepage')">
            </LeetxButton>
            <LeetxButton height="2.5rem" width="2rem" textColor="#3b3b3b" @click="clickLastPage" class="page-btn last-btn"
                font-size="1.375rem" bgcColor="transparent" :text="$t('record.prevpage')">
            </LeetxButton>
            <!-- <LeetxButton textColor="#3b3b3b" height="2.5rem"   v-for="i of getPagesOfArea()" :key="i" :class="{'profile-page':true,'active-page':currentPage==i-1}"  @click="()=>{clickPage(i-1)}"  font-size="1.375rem" bgcColor="transparent" :text="showRealPage(i)"></LeetxButton> -->
            <LeetxButton :textColor="activeBtnColor(i - 1)" height="2.5rem" width="1.5rem" borderRadius="50%"
                v-for="i of getPagesOfArea()" :key="i" :class="{ 'profile-page': true }" @click="() => {
                    clickPage(i - 1);
                }
                    " font-size="1.375rem" :bgcColor="activeBtnBgc(i - 1)" :text="`${showRealPage(i)}`">
            </LeetxButton>
            <LeetxButton height="2.5rem" width="2rem" textColor="#3b3b3b" @click="clickTailPage" class="page-btn tail-btn "
                font-size="1.375rem" bgcColor="transparent" :text="$t('record.endpage')">
            </LeetxButton>
            <LeetxButton height="2.5rem" width="2rem" textColor="#3b3b3b" @click="clickNextPage" class="page-btn next-btn"
                font-size="1.375rem" bgcColor="transparent" :text="$t('record.nextpage')">
            </LeetxButton>
        </div>
        <div v-if="true" class="chart-message">
            <div class="big-box">
                <div :class="['desc-box', isShowDesc ? 'right-border' : 'left-border']" @click="isShowDesc = true">
                    <span v-show="isShowDesc" class="first-triangle"></span>
                    <span v-show="isShowDesc" class="second-triangle"></span>
                    <div class="icon-box">
                        <svg-loader :class="['icon-pic', isShowDesc ? 'color-green' : 'color-3b']" icon="Desc"></svg-loader>
                        <span :class="{ 'strong-font': isShowDesc }">{{ $t("record.desc") }}</span>
                    </div>
                </div>
                <div :class="['chart-btn-box', isShowDesc ? 'left-border' : 'right-border']" @click="isShowDesc = false">
                    <span v-show="!isShowDesc" class="first-triangle"></span>
                    <span v-show="!isShowDesc" class="second-triangle"></span>
                    <div class="icon-box">
                        <svg-loader :class="['icon-pic', !isShowDesc ? 'color-green' : 'color-3b']"
                            icon="OldChart"></svg-loader>
                        <span :class="{ 'strong-font': !isShowDesc }">{{ $t("record.curve") }}</span>
                    </div>
                </div>
            </div>
            <div class="chart-box" v-show="!isShowDesc">
                <sciHisCharts :pdid="pdid" />
            </div>
            <div class="his-chart-desc" v-show="isShowDesc">
                <div>
                    <span>{{ $t("record.tighten_id") }}：{{ lockId }}</span>
                </div>
                <div>
                    <span>{{ $t("record.pset_id") }}：{{ psetId }}</span>
                </div>
                <div>
                    <span>{{ $t("record.torque") }}：{{
                        resTorque
                    }}{{ $store.state.sensors["torque"].name }}</span>
                </div>
                <div>
                    <span>{{ $t("record.torque_range") }}：{{
                        torqueRange
                    }}{{ $store.state.sensors["torque"].name }}</span>
                </div>
                <div>
                    <span>{{ $t("record.angle") }}：{{
                        resAngle
                    }}{{ $store.state.sensors["angle"].name }}</span>
                </div>
                <div>
                    <span>{{ $t("record.angle_range") }}：{{
                        angleRange
                    }}{{ $store.state.sensors["angle"].name }}</span>
                </div>
                <div>
                    <span>{{ $t("record.res_code") }}：{{ resCode }}</span>
                </div>
                <div v-if="!isOk" class="err-color">
                    <span class="reason-desc">{{ $t("record.reason_desc") }}：</span>
                    <p class="reason_desc_span">{{ reasonDescMsg }}</p>
                </div>
                <div v-if="!isOk" class="err-color">
                    <span class="reason-desc"></span><span>{{ errRestDesc }}</span>
                </div>
            </div>
        </div>
        <div v-show="resultShow" class="step-box">
            <div class="show-title">
                <span class="title-msg">{{ $t('record.stepResults') }}</span>
                <div class="close-dlg" @click="cancel">
                    <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
                </div>
            </div>
            <div class="show-box">
                <div class="resultShow-head-line">
                    <span>{{ $t('record.tightenId') }}</span>
                    <span>{{ $t('record.stepIndex') }}</span>
                    <span>{{ $t('record.isOk') }}</span>
                    <span>{{ $t('record.errorCode') }}</span>
                    <span>{{ $t('record.posResult') }}</span>
                    <span>{{ $t('record.torqueResult') }}</span>
                    <span>{{ $t('record.maxAng') }}</span>
                    <span>{{ $t('record.minAng') }}</span>
                    <span>{{ $t('record.maxTorque') }}</span>
                    <span>{{ $t('record.minTorque') }}</span>
                </div>
                <div v-for="(item, key) of getStepResults()" :key="key" class="resultShow-data-line">
                    <span>{{ item.index }}</span>
                    <span>{{ item.step }}</span>
                    <span>{{ getStepIsOk(item) }}</span>
                    <span>{{ item.fail_code }}</span>
                    <span>{{ getStepPos(item) }}</span>
                    <span>{{ getStepSensor(item) }}</span>
                    <span>{{ getStepMaxPos(item) }}</span>
                    <span>{{ getStepMinPos(item) }}</span>
                    <span>{{ getStepMaxSensor(item) }}</span>
                    <span>{{ getStepMinSeneor(item) }}</span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
//历史曲线数据接口
import { pdataResultHisApi } from "../api/history/history";
// 图表组件
import sciHisCharts from "@/components/charts/sciHisCharts.vue";

import filter from "../dialogs/filterHis/filter.js";
import { deepCopy, hasSameStructure } from "../utils/deepCopy";
// import { messgaeApi } from '../components/message/message';
import global_var from "../global/globalVars";
import LeetxButton from "../components/controls/LeetxButton.vue";
// import { saveAs } from 'file-saver';
// import JSZip from 'jszip';
import { postResultsDelete} from "@/api/history/history";
import { messageApi } from '../components/message/message'
import { throttle } from "@/utils/throttle";
import { getresultsdownload } from "@/api/websocketApi";
import IconTextSvgButton from '@/components/controls/IconTextSvgButton.vue'
// import IconTextButton from '@/components/controls/IconTextButton.vue'
import { maskApi } from '@/dialogs/mask/StackMask.vue'
import svgloader from '@/components/icon_font/svgloader.vue'

export default {
    name: "NewProfile",
    components: {
        sciHisCharts,
        LeetxButton,
        IconTextSvgButton,
        svgloader
        // IconTextButton
    },
    data() {
        return {
            module: "RECORDS",
            zip: null,
            activeBtn: 1,
            hisList: [],
            pdid: 0,
            // 选中的历史记录
            activeLine: -1,
            // 当前页区码
            currentArea: -1,
            // 当前区域的页码
            currentPage: -1,
            // 总共几区
            totalArea: 0,
            // 总共几页
            totalPages: 0,
            // 历史曲线总条数
            hisLength: 0,
            // 一页放几条数据
            defaultNum: 11,
            // 一个区放几页
            defaultPages: 7,

            isShowDesc: false,
            hasPermit: false,
            // 筛选条件列表
            conditionsArray: [],
            // 默认筛选条件
            defaultCondition: {
                numcurve: { use: true, val: 10 },
                operator: { use: false, val: "guest" },
                is_ok: { use: false, val: 0 },
                time_start: {
                    use: false,
                    date: { year: 2022, month: 10, day: 12 },
                    time: { hour: 13, min: 24, sec: 34 },
                    dateTimeStr: "2022-10-12 13:24:34",
                },
                time_end: {
                    use: false,
                    date: { year: 2022, month: 10, day: 12 },
                    time: { hour: 15, min: 24, sec: 34 },
                    dateTimeStr: "2022-10-12 15:24:34",
                },
                pset: { use: false, val: 10 },
                errcode: { use: false, val: 'FFFF' },
                has_curve: { use: false, val: 0 }
            },
            // 通过第几条过滤条件进行筛选曲线,-1时候通过默认筛选条件进行筛选
            currentPointer: -1,
            isShowCndtnsArray: false,
            currentCndtnTextShow: "",
            currentCndtnText: "",
            throttleFn: null,
            rotate: false,
            maskId: 0,
            resultShow: false,
            activeCurrentLine: null,
            selectedResultData: null,
            isStepResults: false,
        };
    },
    methods: {
        selectLine(item) {
            this.activeCurrentLine = item; // 更新选中行的信息
        },
        multiStepResult() {
            this.rotate = !this.rotate
            this.maskId = maskApi.rise(15, this.cancel)
            this.resultShow = true
        },
        cancel() {
            // this.isShow=false
            this.resultShow = false
            this.rotate = false
            this.onCancel && this.onCancel("用户取消了")
            maskApi.descend(this.maskId)
        },
        // 物理量
        sensorUnit(uname) {
            return this.$store.state.sensors[uname].name;
        },
        // 根据物理量的值及变换关系计算出展示的数据值
        displayVal(sname, oldval) {
            var sensor = this.$store.state.sensors[sname];
            if (sensor == null) {
                sensor = {
                    gain: 1,
                    offset: 0,
                };
            }
            const res = (parseFloat(oldval) * sensor.gain + sensor.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 历史曲线接口
        getHisApi(success, failure, condition = {}) {
            pdataResultHisApi(condition)
                .then((result) => {
                    success && success(result);
                })
                .catch((err) => {
                    failure && failure(err);
                });
        },
        // 根据条件获取历史曲线数据,该条件是最终传给后台的条件
        getHisData(condition) {
            let success = (result) => {
                this.hisList = [];
                this.hisList = result.data;
                let numText = ` ${this.$t("record.numcurve")}:${this.hisList.length}`
                this.currentCndtnTextShow = this.currentCndtnText + numText
                this.initPagesAndAreas();
                this.showFirstChart();
                this.hasPermit = true;
            };
            let failure = (err) => {
                if (err.code == 403) {
                    this.hasPermit = false;
                }
            };
            this.getHisApi(success, failure, condition);
            // this.getVisualData()
        },
        // 创建假数据

        statusClass(ok) {
            if (ok === 1) {
                return "ok";
            }
            return "nok";
        },
        // 初始化分区的数量和总的页数
        initPagesAndAreas() {
            this.hisLength = this.hisList.length;
            this.totalPages = Math.ceil(this.hisLength / this.defaultNum);
            this.totalArea = Math.ceil(this.totalPages / this.defaultPages);
            // 0区
            this.currentArea = this.totalArea > 0 ? 0 : -1;
            // 0页
            this.currentPage = this.totalPages > 0 ? 0 : -1;
        },
        // 获取区域的页码数量
        getPagesOfArea() {
            if (this.hisLength == 0) {
                return 0;
            }
            if ((this.currentArea + 1) * this.defaultPages > this.totalPages) {
                return this.totalPages - this.currentArea * this.defaultPages;
            } else {
                return this.defaultPages;
            }
        },
        // 获取页区的条数
        getNumOfPage() {
            if (this.hisLength == 0) {
                return 0;
            }
            let nums =
                (this.currentArea * this.defaultPages + this.currentPage) * this.defaultNum;
            if (nums + this.defaultNum > this.hisLength) {
                return this.hisLength - nums;
            } else {
                return this.defaultNum;
            }
        },
        // 首页
        clickHeadPage() {
            if (this.hisLength == 0) {
                return 0;
            }
            this.currentArea = 0;
            this.currentPage = 0;
            this.showRealIndexChart(this.activeLine);
        },
        // 尾页
        clickTailPage() {
            if (this.hisLength == 0) {
                return 0;
            }
            this.currentArea = this.totalArea - 1;
            this.currentPage = this.totalPages - this.defaultPages * this.currentArea - 1;
            this.showRealIndexChart(this.activeLine);
        },
        // 下一页
        clickNextPage() {
            if (this.hisLength == 0) {
                return 0;
            }
            if (
                this.currentArea == this.totalArea - 1 &&
                this.currentPage == this.totalPages - this.currentArea * this.defaultPages - 1
            ) {
                this.currentArea = 0;
                this.currentPage = 0;
            } else if (this.currentPage == this.defaultPages - 1) {
                if (this.currentArea == this.totalArea - 1) {
                    this.currentArea = this.totalArea - 1;
                    this.currentPage = this.defaultPages - 1;
                } else {
                    this.currentArea += 1;
                    this.currentPage = 0;
                }
            } else {
                this.currentPage += 1;
            }
            this.showRealIndexChart(this.activeLine);
        },
        // 上一页
        clickLastPage() {
            if (this.hisLength == 0) {
                return 0;
            }
            if (this.currentArea == 0) {
                if (this.currentPage == 0) {
                    this.currentArea = this.totalArea - 1;
                    this.currentPage = this.totalPages - this.currentArea * this.defaultPages - 1;
                } else if (this.currentPage != 0) {
                    this.currentPage -= 1;
                }
            } else {
                if (this.currentPage == 0) {
                    this.currentArea -= 1;
                    this.currentPage = this.defaultPages - 1;
                } else {
                    this.currentPage -= 1;
                }
            }
            this.showRealIndexChart(this.activeLine);
        },
        // 展示页码
        showRealPage(page) {
            let realPage = this.currentArea * this.defaultPages + page;
            return realPage;
        },
        // 点击带数字的分页按钮
        clickPage(i) {
            if (this.hisLength == 0) {
                return 0;
            }
            this.currentPage = i;
            this.showRealIndexChart(this.activeLine);
        },
        // 获取选中的项目在histlist里面真正的索引值
        getRealIndex(item) {
            let index = (this.currentArea * this.defaultPages + this.currentPage) * this.defaultNum + item - 1;
            // return index>this.hisLength-1?this
            if (index > this.hisLength - 1) {
                this.activeLine = 1;
                return this.hisLength - 1;
            }
            return index;
        },
        // 通过pdid的变化由子组件展示相应的历史曲线
        showRealIndexChart(line) {
            if (line == -1) {
                return;
            }
            this.activeLine = line;
            let item = this.getCurrentHisItem();
            if (item) {
                this.pdid = this.$store.state.f_status.environment_is_node ? item.tid : item.pdid;
            }

        },
        // 展示过滤条件对话框
        showFilterDlg() {
            // if(!this.hasPermit){return}
            this.openCndtnsArray(false);
            let condition = this.getCurrentCondition();
            condition.download = false
            filter({ condition }).then((result) => {
                //点击筛选-确定后接收到新的筛选条件
                this.handleCondition(result.newCondition);
            });
        },

        convertToCSV(arr) {
            const separator = ",";
            const keys = Object.keys(arr[0]);
            const csvHeader = keys.join(separator);
            const csvRows = arr.map(row => {
                return keys.map(key => {
                    return row[key];
                }).join(separator);
            });
            return csvHeader + "\n" + csvRows.join("\n");
        },

        curveconvertToCSV(curvedata, len) {
            const separator = ",";
            const csvHeader = "times,positions,sensor_vals,speeds" + "\n";
            const times = curvedata.slice(len * 2, len * 3);
            const positions = curvedata.slice(len, len * 2);
            const sensor_vals = curvedata.slice(0, len);
            const speeds = curvedata.slice(len * 3, len * 4);
            let csvrows = ""
            for (let i = 0; i < len; i++) {
                csvrows += ([times[i], positions[i], sensor_vals[i], speeds[i]].join(separator) + "\n")

            }
            return csvHeader + csvrows;
        },
        // 导出文件名称
        formatDate() {
            let date = new Date();
            let year = date.getFullYear();
            let month = String(date.getMonth() + 1).padStart(2, '0');
            let day = String(date.getDate()).padStart(2, '0');
            let hour = String(date.getHours()).padStart(2, '0');
            let minute = String(date.getMinutes()).padStart(2, '0');
            let second = String(date.getSeconds()).padStart(2, '0');
            const formattedDate = `result_${year}${month}${day}-${hour}${minute}${second}`;
            return formattedDate
        },


        // 展示下载条件对话框
        shoDownloadDlg() {
            // if(!this.hasPermit){return}
            this.openCndtnsArray(false);
            let condition = this.getCurrentCondition();
            condition.download = true;
            console.log("+++++++",condition)
            filter({ condition }).then((result) => {
                //点击下载-确定后接收到新的下载条件
                let downloadCndtn = this.getToServerCondition(result.newCondition);
                // 从本地导出文件        
                getresultsdownload.send(JSON.stringify(downloadCndtn));
            });
        },
        // 导出文件
        exportFile() {
            if (this.module in this.$store.state.f_status.permissions &&  this.$store.state.f_status.permissions[this.module] != 4){
                return
            }
            if (this.$store.state.f_status.environment_is_node){
                return
            }
            // this.cursor ? this.exportFileBrower() : this.shoDownloadDlg()
            this.shoDownloadDlg()
        },
        downloadFromServer(name){
            const serverUrl = `${window.location.origin}/api/pdata/results/download?filename=${name}`;

            const downloadLink = document.createElement('a');

            downloadLink.href = serverUrl;
            downloadLink.download = name;
            downloadLink.click();
            // URL.revokeObjectURL(blobUrl);
        },

        // 重设数据
        resetData() {
            this.activeBtn = 1;
            this.hisList = [];
            this.pdid = 0;
            this.activeLine = -1;
            this.currentArea = -1;
            this.currentPage = -1;
            this.totalArea = 0;
            this.hisLength = 0;
        },
        // 重新根据历史条件获取数据
        updateHisTableData() {
            if (!this.hasPermit) {
                return;
            }
            let currentCndtn = this.getCurrentCondition();
            let oldCondition = this.getToServerCondition(currentCndtn);
            this.getHisData(oldCondition);
        },
        getStepResults() {
            let item = this.getCurrentHisItem();
            if (item == undefined) {
                return []
            }
            return item.result;
        },
        // 展示pset号
        getPset(item) {
            return this.hisList[this.getRealIndex(item)].pset;
        },
        // 是否合格
        getOk(item) {
            return this.hisList[this.getRealIndex(item)].is_ok == 1
                ? this.$t("record.qualified_ok")
                : this.hisList[this.getRealIndex(item)].code;
        },
        //分步是否合格
        getStepIsOk(item) {
            return item.is_ok == 1
                ? this.$t("record.qualified_ok")
                : this.$t("record.qualified_nok")
        },
        // 展示位置
        getPos(item) {
            let val = this.hisList[this.getRealIndex(item)].ret_pos;
            let unit = this.$store.state.sensors["angle"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示分步位置
        getStepPos(item) {
            let val = item.pos
            let unit = this.$store.state.sensors["angle"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示扭矩
        getSensor(item) {
            let val = this.hisList[this.getRealIndex(item)].ret_sensor;
            let unit = this.$store.state.sensors["torque"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示分步扭矩
        getStepSensor(item) {
            let val = item.sensor
            let unit = this.$store.state.sensors["torque"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示分布扭矩
        getStepMaxSensor(item) {
            let val = item.max_sensor
            let unit = this.$store.state.sensors["torque"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        getStepMinSeneor(item) {
            let val = item.min_sensor
            let unit = this.$store.state.sensors["torque"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示分布位置
        getStepMaxPos(item) {
            let val = item.max_pos
            let unit = this.$store.state.sensors["angle"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        getStepMinPos(item) {
            let val = item.min_pos
            let unit = this.$store.state.sensors["angle"];
            const res = (unit.gain * val + unit.offset).toFixed(3);
            return isNaN(res) ? "--" : res;
        },
        // 展示时间
        getTime(item) {
            const res = this.hisList[this.getRealIndex(item)].created_time.slice(0, 19);
            return res;
        },
        // 展示当前区当前页的第一条数据
        showFirstChart() {
            setTimeout(() => {
                this.showRealIndexChart(1);
            }, 500);
        },
        handleCondition(condition) {
            // 有没有相同的过滤条件
            let index = this.isConditionsArrayHasNewCondition(condition);
            // 往历史过滤条件列表添加新的过滤条件
            if (index == -1) {
                index = 0;
                this.conditionsArray.splice(0, 0, condition);
                while (this.conditionsArray.length > 20) {
                    this.conditionsArray.pop();
                }
                global_var.filterCndtns = deepCopy(this.conditionsArray);
            }
            // else{
            //     let msgOne=this.$t('record.msgThree')
            //     let msgTwo=this.$t('record.msgFour')
            //     let row=this.$t('record.row')
            //     messgaeApi.warning([msgOne,`${msgTwo} ${index+1}${row}`])
            //     (msgOne,`${msgTwo} ${index+1}${row}`);
            // }
            this.updateCurrentCndtnPointer(index);
            let filterCndtn = this.getToServerCondition(condition);
            // (filterCndtn);
            this.getHisData(filterCndtn);
        },
        // 根据条件添加要过滤的字段
        getToServerCondition(condition) {
            let filterCndtn = {};
            // 曲线条数
            if (condition.numcurve.use) {
                filterCndtn["numcurve"] = condition.numcurve.val;
            }
            // 是否启用合格
            if (condition.is_ok.use) {
                filterCndtn["is_ok"] = condition.is_ok.val;
            }
            // 是否启用时间范围
            if (condition.time_start.use) {
                filterCndtn["time_start"] = condition.time_start.dateTimeStr;
                filterCndtn["time_end"] = condition.time_end.dateTimeStr;
            }
            // 是否通过pset号进行筛选
            if (condition.pset.use) {
                filterCndtn["pset"] = condition.pset.val;
            }
            if (condition.errcode.use) {
                filterCndtn["code"] = condition.errcode.val;
            }
            filterCndtn["has_curve"] = condition.has_curve.use;
            filterCndtn["to_usb"] = !this.cursor;
            return filterCndtn;
        },
        // 获取当前选中的hisitem项
        getCurrentHisItem() {
            let item = undefined;
            if (this.activeLine != -1 && this.hisList.length > 0) {
                item = this.hisList[this.getRealIndex(this.activeLine)];
                if (item.result.length < 2) {
                    this.isStepResults = false;
                } else {
                    this.isStepResults = true;
                }
            }
            return item;
        },
        getCurrentCondition() {
            // let cndtn=this.conditionsArray[this.currentPointer]
            let cndtn = undefined;
            if (this.conditionsArray.length > 0 && this.currentPointer != -1) {
                cndtn = this.conditionsArray[this.currentPointer];
            }
            // 找不到选中的过滤条件就返回默认过滤条件
            return cndtn ? cndtn : this.defaultCondition;
        },
        updateCurrentCndtnPointer(i) {
            this.currentPointer = i;
            let currnetCndtn = this.getCurrentCondition();
            let filterCndtn = this.getToServerCondition(currnetCndtn);
            this.getHisData(filterCndtn);
            this.currentCndtnText = this.showCondtionText(currnetCndtn);
            this.openCndtnsArray(false);
        },
        delCndtnFromConditionsArray(i) {
            this.conditionsArray.splice(i, 1);
            this.currentPointer = this.conditionsArray.length == 0 ? -1 : this.currentPointer;
        },
        openCndtnsArray(val) {
            setTimeout(() => {
                this.isShowCndtnsArray = val;
            }, 100);
        },
        showCondtionText(condition) {
            // if(!this.hasPermit){return ""}
            if (!condition) {
                let msg = this.$t("record.msgTwo");
                return msg;
            }
            let psetText = condition.pset.use
                ? `${this.$t("record.pset_id")}${condition.pset.val} `
                : "";
            let dateTimeText = condition.time_start.use
                ? ` ${this.$t("record.timeRange")} ${condition.time_start.dateTimeStr} ~ ${condition.time_end.dateTimeStr
                } `
                : "";
            // 是否合格,是否展示错误码
            let okText = condition.is_ok.use
                ? condition.is_ok.val == 1
                    ? ` ${this.$t("record.qualified_ok")} `
                    : condition.errcode.use
                        ? `${this.$t("record.errCode")}${condition.errcode.val}`
                        : `${this.$t("record.qualified_nok")}`
                : "";
            // let numText = condition.numcurve.use
            //     ? ` ${this.$t("record.numcurve")}:${condition.numcurve.val} `
            //     : "";
            // return `${psetText} ${dateTimeText} ${okText} ${numText}`;
            return `${psetText} ${dateTimeText} ${okText}`;
        },
        isConditionsArrayHasNewCondition(condition) {
            for (let i = 0; i < this.conditionsArray.length; i++) {
                if (hasSameStructure(this.conditionsArray[i], condition, true)) {
                    return i;
                }
            }
            return -1;
        },
        initDefaultCndtn() {
            let now = new Date();
            let year = now.getFullYear();
            let month = now.getMonth() + 1;
            let day = now.getDate();
            let hour = now.getHours();
            let min = now.getMinutes();
            let sec = now.getSeconds();
            let date = { year, month, day };
            let time = { hour, min, sec };
            while (`${year}`.length < 4) {
                year = `0${year}`;
            }
            day = day < 10 ? `0${day}` : `${day}`;
            month = month < 10 ? `0${month}` : `${month}`;
            hour = hour < 10 ? `0${hour}` : `${hour}`;
            min = min < 10 ? `0${min}` : `${min}`;
            sec = sec < 10 ? `0${sec}` : `${sec}`;
            this.defaultCondition = {
                numcurve: { use: true, val: 10 },
                operator: { use: false, val: "guest" },
                is_ok: { use: false, val: 0 },
                time_start: {
                    use: false,
                    date,
                    time,
                    dateTimeStr: `${year}-${month}-${day} ${hour}:${min}:${sec}`,
                },
                time_end: {
                    use: false,
                    date,
                    time,
                    dateTimeStr: `${year}-${month}-${day} ${hour}:${min}:${sec}`,
                },
                pset: { use: false, val: 10 },
                errcode: { use: false, val: 'FFFF' },
                has_curve: { use: false, val: 0 }
            };
            this.currentCndtnText = this.showCondtionText(this.defaultCondition);
        },
        initConditionsArray() {
            this.conditionsArray = deepCopy(global_var.filterCndtns);
            while (this.conditionsArray.length > 20) {
                this.conditionsArray.pop();
            }
        },
        activeBtnBgc(i) {
            return this.currentPage == i ? "#00c155" : "transparent";
        },
        activeBtnColor(i) {
            return this.currentPage == i ? "#ffffff" : "#3b3b3b";
        },
        createStatusWs() {
            getresultsdownload.msghandler = (msg) => {
                let result = JSON.parse(msg);
                switch (result.code) {
                    case 201:
                        messageApi.success([this.$t('download.success201'), this.$t('download.downloading')])
                        break;
                    case 202:
                        messageApi.success([this.$t('download.success202', result.data), this.$t('download.downloading')])
                        break;
                    case 203:
                        messageApi.success([this.$t('download.success203'), this.$t('download.downloading')])
                        break;
                    case 204:
                        messageApi.success([this.$t('download.success204', result.data), this.$t('download.downloading')])
                        break;
                    case 205:
                        messageApi.success([this.$t('download.success205', result.data), this.$t('download.downloading')])
                        break;
                    case 206:
                        messageApi.success([this.$t('download.success206'), this.$t('download.downloading')])
                        break;
                    case 207:
                        messageApi.success([this.$t('download.success207', result.data), ""])
                        break;
                    case 208:
                        this.downloadFromServer(result.name)
                        postResultsDelete(result.name)
                        break;
                    case 404:
                        messageApi.error([this.$t('download.error404'), ""])
                        break;
                    case 405:
                        messageApi.error([this.$t('download.error405'), ""])
                        break;
                    case 406:
                        messageApi.error([this.$t('download.error406'), this.$t('download.error406Two')])
                        break;
                    default:
                        break;
                }
            };
            getresultsdownload.openhandler = () => {
                console.log("getresultsdownload ws connected !!!");
                // getresultsdownload.send("hello world!")

                // defineIsInit = false;
            };
            getresultsdownload.closehandler = () => {
                // defineIsInit = false;
                console.log("getresultsdownload ws disconnected !!!");
                // let msg = this.$t('download.downloadFailWs')
                // messageApi.error([msg])
                // STORES.state.g_status = { ...defaultGStatus };
                // setTimeout(createStatusWs, 3000);
            };
            getresultsdownload.close()
            getresultsdownload.open()
        },
    },
    computed: {
        download() {
            return "Download"
        },
        // messageClass() {
        //     return "normal";
        // },
        dateTime() {
            if (this.activeLine != -1) {
                let item = this.getCurrentHisItem();
                return item ? item.created_time.slice(0, 19) : "";
            }
            return "";
        },
        operator() {
            let item = this.getCurrentHisItem();
            return item ? item.operator : "";
        },
        resCode() {
            let item = this.getCurrentHisItem();
            return item
                ? item.is_ok == 1
                    ? `${this.$t("record.qualified_ok")}`
                    : item.code
                : "";
        },
        reasonDescMsg() {
            let item = this.getCurrentHisItem();
            return item
                ? item.is_ok == 1
                    ? `${this.$t("record.qualified_ok")}`
                    : item.code_desc
                : "";
        },
        resAngle() {
            let item = this.getCurrentHisItem();
            if (!item) {
                return "";
            }
            let val = item.ret_pos;
            let unit = this.$store.state.sensors["angle"];
            return (unit.gain * val + unit.offset).toFixed(3);
        },
        resTorque() {
            let item = this.getCurrentHisItem();
            if (!item) {
                return "";
            }
            let val = item.ret_sensor;
            let unit = this.$store.state.sensors["torque"];
            return (unit.gain * val + unit.offset).toFixed(3);
        },
        angleRange() {
            let item = this.getCurrentHisItem();
            if (item) {
                let minAngle =
                    item.pd_min_pos * this.$store.state.sensors["angle"].gain +
                    this.$store.state.sensors["angle"].offset;
                let maxAngle =
                    item.pd_max_pos * this.$store.state.sensors["angle"].gain +
                    this.$store.state.sensors["angle"].offset;
                return `${minAngle.toFixed(3)}~${maxAngle.toFixed(3)}`;
            }
            return `****~****`;
        },
        torqueRange() {
            let item = this.getCurrentHisItem();
            if (item) {
                let minTorque =
                    item.pd_min_sensor * this.$store.state.sensors["torque"].gain +
                    this.$store.state.sensors["torque"].offset;
                let maxTorque =
                    item.pd_max_sensor * this.$store.state.sensors["torque"].gain +
                    this.$store.state.sensors["torque"].offset;
                return `${minTorque.toFixed(3)}~${maxTorque.toFixed(3)}`;
            }
            return `****~****`;
        },
        errDesc() {
            let item = this.getCurrentHisItem();
            if (item && item.desc) {
                let str = item.desc;
                return str.length > 27 ? str.slice(0, 27) : str;
            }
            return "";
        },
        errRestDesc() {
            let item = this.getCurrentHisItem();
            if (item && item.desc) {
                let str = item.desc;
                return str.length > 27 ? str.slice(27, str.length) : "";
            }
            return "";
        },
        lockId() {
            let item = this.getCurrentHisItem();
            return item ? `[ ${item.tid} ] \t SN: [ ${item.sn ?? "--"} ] ` : "";
        },
        psetId() {
            let item = this.getCurrentHisItem();
            return item ? item.pset : "";
        },
        isOk() {
            let item = this.getCurrentHisItem();
            return item ? item.is_ok === 1 : "";
        },
        cursor() {
            return window.navigator.userAgent.toLowerCase().includes("windows");
        },
    },
    created() {
        this.initDefaultCndtn();
        this.initConditionsArray();
        this.throttleFn = throttle(this.showRealIndexChart, 500)
    },
    activated() {
        let currentCndtn = this.getCurrentCondition();
        let oldCondition = this.getToServerCondition(currentCndtn);
        this.getHisData(oldCondition);
    },
    watch: {},
    mounted() {
        this.createStatusWs()
    },
};
</script>

