<template>
    <div class="mask-box" v-if="isShow" @click="cancel()">
        <div class="date-time-box" @click.stop>
            <div class="calendar-box">
                <div class="date-box">
                    <svgloader icon_style="svg-loader" color="black" icon="Double_Left" @click="this.year -= 1" />
                    <span class="sub-month" @click="subMonth"></span>
                    <span class="date-text">{{ year }}<strong>.</strong>{{ showMonth }}</span>
                    <span class="add-month" @click="addMonth"></span>
                    <svgloader icon_style="svg-loader-right" color="black" icon="Double_Right" @click="this.year += 1" />
                </div>
                <table>
                    <tr>
                        <td><span :class="{ 'fnt3B': firstWeek == 0 }">日</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 1 }">一</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 2 }">二</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 3 }">三</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 4 }">四</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 5 }">五</span></td>
                        <td><span :class="{ 'fnt3B': firstWeek == 6 }">六</span></td>
                    </tr>
                    <tr v-for="i of 6" :key="i">
                        <td v-for="j of 7" :key="j">
                            <span v-show="days[7 * i + j - 8].status == 'current'" :class="dateClass(7 * i + j - 8)"
                                @click="handleDate(7 * i + j - 8)">{{ showDay(7 * i + j - 8) }}</span>
                        </td>
                    </tr>
                </table>
                <div class="line"></div>
            </div>
            <div class="time-box">
                <div class="add-btns">
                    <div @click="handlebtn(0)"><span><strong>+</strong></span></div>
                    <div></div>
                    <div @click="handlebtn(1)"><span><strong>+</strong></span></div>
                    <div></div>
                    <div @click="handlebtn(2)"><span><strong>+</strong></span></div>
                </div>
                <div class="time-text">
                    <div class="click-box" @click="updateTime('hour')"><span :class="{ 'box-active': hourIsActive }">{{
                        hourText }}</span></div>
                    <div><strong>:</strong></div>
                    <div class="click-box" @click="updateTime('min')"><span :class="{ 'box-active': minIsActive }">{{
                        minText }}</span></div>
                    <div><strong>:</strong></div>
                    <div class="click-box" @click="updateTime('sec')"><span :class="{ 'box-active': secIsActive }">{{
                        secText }}</span></div>
                </div>
                <div class="sub-btns">
                    <div @click="handlebtn(3)"><span><strong>-</strong></span></div>
                    <div></div>
                    <div @click="handlebtn(4)"><span><strong>-</strong></span></div>
                    <div></div>
                    <div @click="handlebtn(5)"><span><strong>-</strong></span></div>
                </div>
            </div>
            <LeetxButton class="cancel" :text="$t('confirm.cancel')" fontSize="26px" @click="cancel">
            </LeetxButton>
            <LeetxButton class="btn" :text="$t('confirm.validity')" fontSize="26px" textColor="#fff" bgcColor="#00c155"
                @click="submitDateTime">
            </LeetxButton>

        </div>
    </div>
</template>
<script>
import showNumkybrd from '../../keyboard/nbkbrd/nbkybrd'
import LeetxButton from '../controls/LeetxButton.vue'
import svgloader from '@/components/icon_font/svgloader'
import { bus } from '@/main.js'
export default {
    components: { LeetxButton, svgloader },
    data() {
        return {
            year: (new Date().getFullYear()),
            month: (new Date().getMonth()) + 1,
            days: [],
            day: { status: "current", 'val': (new Date().getDate()) },
            firstWeek: undefined,
            lastDay: undefined,
            activeDay: -1,
            isEditDate: false,
            hour: new Date().getHours(),
            min: new Date().getMinutes(),
            sec: new Date().getSeconds(),
            isShow: true,
            hourIsActive: false,
            minIsActive: false,
            secIsActive: false,
        }
    },
    methods: {
        addMonth() {
            if (this.month == 12) {
                this.year += 1
                this.month = 1
            } else {
                this.month += 1
            }
        },
        subMonth() {
            if (this.month == 1) {
                this.year -= 1
                this.month = 12
            } else {
                this.month -= 1
            }
        },
        addYear() {
            this.year += 1
        },
        subYear() {
            this.year -= 1
        },
        // 获取某个月的第一个星期几
        getFirstWeek(year, month) {
            return new Date(year, month - 1, 1).getDay()
        },
        showDay(i) {
            let day = this.days[i]['val']
            return day < 10 ? `0${day}` : `${day}`
        },
        // 获取某个月的最后一天
        getLastDay(year, month) {
            return new Date(year, month, 0).getDate()
        },
        updateTable() {
            this.days = []
            this.firstWeek = this.getFirstWeek(this.year, this.month)
            this.lastDay = this.getLastDay(this.year, this.month)
            let lastMonthLastDay = this.getLastDay(this.year, this.month - 1)
            for (let day = lastMonthLastDay - this.firstWeek + 1; day < lastMonthLastDay + 1; day++) {
                this.days.push({ "val": day, "status": 'last' })
            }
            for (let day = 1; day < this.lastDay + 1; day++) {
                if (day == this.day.val) {
                    this.activeDay = this.days.length
                }
                this.days.push({ "val": day, "status": 'current' })
            }
            this.activeDay = this.days[this.days.length - 1]['val'] >= this.activeDay ? this.activeDay : this.days[this.days.length - 1]['val']
            for (let day = 1; day < 42 - this.lastDay - this.firstWeek + 2; day++) {
                this.days.push({ "val": day, "status": 'next' })
            }
            this.firstWeek = this.activeDay % 7

        },
        initDateTime() {
            this.year = this.date.year ? this.date.year : this.year;
            this.month = this.date.month ? this.date.month : this.month;
            this.day = { status: "current", 'val': this.date.day ? this.date.day : this.day }
            this.hour = this.time.hour ? this.time.hour : this.hour;
            this.min = this.time.min ? this.time.min : this.min;
            this.sec = this.time.sec ? this.time.sec : this.sec;
            this.year = this.year > 2020 ? this.year : 2020;
        },
        handleDate(i) {
            let day = this.days[i]
            this.day = day
            this.activeDay = i
            if (day.status == 'current') {
                this.activeDay = i
            }
            else if (day.status == "last") {
                setTimeout(() => {
                    this.subMonth()
                }, 200)

            } else {
                setTimeout(() => {
                    this.addMonth()
                }, 200)
            }
        },
        dateClass(i) {
            if (this.days[i].status != "current") {

                return 'not-current'
            }
            if (i == this.activeDay) {
                return ['current', 'active-day']
            }
            return 'current'
        },

        getYear(year) {
            this.year = parseInt(year)
        },
        getMonth(month) {
            this.month = parseInt(month)
        },
        handlebtn(i) {
            switch (i) {
                case 0:
                    this.hour = this.hour == 23 ? 0 : this.hour + 1
                    return
                case 1:
                    this.min = this.min == 59 ? 0 : this.min + 1
                    return
                case 2:
                    this.sec = this.sec == 59 ? 0 : this.sec + 1
                    return
                case 3:
                    this.hour = this.hour == 0 ? 23 : this.hour - 1
                    return
                case 4:
                    this.min = this.min == 0 ? 59 : this.min - 1
                    return
                case 5:
                    this.sec = this.sec == 0 ? 59 : this.sec - 1
                    return
            }
        },
        initTime() {
            this.hour = this.time.hour
            this.min = this.time.min
            this.sec = this.time.sec
        },
        submitDateTime() {
            let year = this.year
            let month = this.month
            let day = this.day.val
            let hour = this.hour
            let min = this.min
            let sec = this.sec
            let date = { year, month, day }
            let time = { hour, min, sec }
            while (`${year}`.length < 4) {
                year = `0${year}`
            }
            month = this.month < 10 ? `0${this.month}` : this.month
            day = this.day.val < 10 ? `0${this.day.val}` : this.day.val
            hour = this.hour < 10 ? `0${this.hour}` : this.hour
            min = this.min < 10 ? `0${this.min}` : this.min
            sec = this.sec < 10 ? `0${this.sec}` : this.sec

            let dateTimeStr = `${year}-${month}-${day} ${hour}:${min}:${sec}`
            this.onConfirm && this.onConfirm({ dateTimeStr, date, time })
            this.isShow = false
            this.closeNumkb()

        },
        cancel() {
            this.onCancel && this.onCancel()
            this.isShow = false
            this.closeNumkb()
        },
        updateTime(key) {
            if (key == 'hour') {
                this.hourIsActive = true
                this.minIsActive = false
                this.secIsActive = false
            } else if (key == 'min') {
                this.minIsActive = true
                this.hourIsActive = false
                this.secIsActive = false
            } else if (key == 'sec') {
                this.secIsActive = true
                this.hourIsActive = false
                this.minIsActive = false
            }
            const clbck = (val) => {
                this[key] = val
            }
            let min = 0
            let max = key == "hour" ? 23 : 59
            let topY = 4.6 + 'rem'
            showNumkybrd({
                clbck,
                min,
                max,
                isInt: true,
                topY,
                val: this[key]
            }).then((result) => {
                clbck(result)
            }).catch(() => {
            });
        },
        closeNumkb() {
            bus.emit('closeNumkb')
        }

    },
    watch: {
        month() {
            this.updateTable()
        },
        year() {
            this.updateTable()
        },
        date() {
            this.initDateTime()
        },
        time: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initTime()
            }
        }

    },
    created() {
        this.initDateTime()
        this.updateTable()

    },
    computed: {
        showMonth() {
            return this.month < 10 ? `0${this.month}` : this.month
        },
        hourText() {
            return this.hour < 10 ? `0${this.hour}` : this.hour
        },
        minText() {
            return this.min < 10 ? `0${this.min}` : this.min
        },
        secText() {
            return this.sec < 10 ? `0${this.sec}` : this.sec
        }
    },
    props: {
        date: {
            type: Object,
            default: function () {
                return {
                    year: new Date().getFullYear(),
                    month: new Date().getMonth() + 1,
                    day: { status: "current", 'val': (new Date().getDate()) },
                }
            }
        },
        time: {
            type: Object,
            default: function () {
                return { hour: new Date().getHours(), min: new Date().getMinutes(), sec: new Date().getSeconds() }
            }
        },
        clbck: {
            type: Function,
            default: function () { }
        }
    }
}
</script>
<style scoped>
.svg-loader {
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
    left: calc(50% - 11rem);
    top: 50%;
    transform: translateY(-50%);
}

.svg-loader-right {
    display: block;
    width: 2rem;
    height: 2rem;
    position: absolute;
    right: calc(50% - 11rem);
    top: 50%;
    transform: translateY(-50%);
}

.mask-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: transparent;
    z-index: 59;
}

.date-time-box {
    width: 37.5rem;
    height: 47.5rem;
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    background-color: var(--fff);
    border-radius: 6px;
    z-index: 62;
}

* {
    margin: 0;
    padding: 0;
}

.calendar-box {
    position: relative;
    width: 37.5rem;
    box-sizing: border-box;
    border-radius: 6px 6px 0px 0px;
    overflow: hidden;
}

.date-box {
    position: relative;
    left: 0;
    top: 0;
    width: 37.5rem;
    height: 3.269rem;
    background-color: var(--Cee);

}

.date-text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 2.25rem;
    border-radius: 4px;

}

.add-month {
    display: inline-block;
    width: 10px;
    height: 10px;
    position: absolute;
    left: calc(50% + 6rem);
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    border-right: 4px solid var(--C65);
    border-top: 4px solid var(--C65);
}

.sub-month {
    display: inline-block;
    width: 10px;
    height: 10px;
    position: absolute;
    left: calc(50% - 7rem);
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
    border-left: 4px solid var(--C65);
    border-bottom: 4px solid var(--C65);
}

button {
    width: 4rem;
    height: 2rem;
}

table {
    position: relative;

    width: 37.5rem;
    height: 25rem;
    display: flex;
    flex-direction: column;
}

table tr {
    flex: 1;
    display: flex;
}

table tr td {
    flex: 1;
    border: 2px solid transparent;
    position: relative;
}

table tr td span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    font-size: var(--font1_625);
    height: 2.5rem;
    width: 2.5rem;
    box-sizing: border-box;
    color: var(--C3B);
    text-align: center;
    padding-top: 0.2rem;
    border-radius: 50%;
}

.fnt3B {
    font-weight: 600;
    color: var(--C00) !important;
}

table tr:first-child td span {

    font-size: var(--font1_625);
    color: var(--C96);
}

table tr:nth-child(n+2) td {
    box-sizing: border-box;
    cursor: pointer;
}

table tr:nth-child(n+2) td span:active {
    background-color: var(--C00c155);

}

table tr:nth-child(n+1) td:nth-child(-n+6) {
    margin-right: 2px;
}

table tr:nth-child(-n+7) td {
    margin-bottom: 2px;
}

.current {
    color: black;
}

.not-current {
    color: #c9c4cc;
}

.date-btn {
    position: relative;
    left: 1rem;
}

.active-day {
    color: var(--fff);
    background-color: var(--C00c155);
}

.line {
    width: 33.75rem;
    height: 1px;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--Cd4);
}

.san-jiao {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: 2.8rem;
    border: 0.625rem solid transparent;
    border-bottom: 0.625rem solid rgba(0, 0, 0, 0.7);


}

.edit-box {
    position: absolute;
    top: 0.525rem;
    left: 50%;
    transform: translateX(-50%);
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 10px;
    width: 12rem;
    height: 7rem;

}

.edit-box span {
    font-size: 1.8rem;
    display: block;
    width: 100%;
    height: 3.5rem;
    line-height: 3.5rem;
    text-align: center;
    color: var(--fff);
    white-space: nowrap !important;
}

.edit-box span:active {
    color: var(--C00c155);
}

.time-box {
    position: relative;
    width: 37.5rem;
    box-sizing: border-box;
    padding: 1rem;
    z-index: 41;
}

.add-btns,
.sub-btns,
.time-text {
    display: flex;
    width: 30rem;
    margin-bottom: 1rem;
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}

.add-btns div,
.sub-btns div,
.time-text div {
    flex: 1;
    text-align: center;
}

.box-active {
    color: var(--C00c155) !important;
    border: 2px solid var(--C00c155) !important;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

div .click-box span {
    display: inline-block;
    height: 2rem;
    width: 2rem;
    border-radius: 2px;
    border: 2px solid gainsboro;
}

.time-text span {
    font-size: var(--font1_625);
    font-weight: normal;
}

.sub-btns div span,
.add-btns div span {
    width: 2rem;
    height: 2rem;
    line-height: 1.5rem;
    display: inline-block;
    font-size: 2rem;
    color: var(--C3B) !important;
}

.add-btns span:nth-child(2n-1),
.sub-btns span:nth-child(2n-1) {
    cursor: pointer;
    background-color: var(--Cf1);
    border-radius: 5px;
}

.btn {
    text-align: center;
    position: absolute;
    top: 86%;
    width: 8.625rem;
    height: 3.125rem;
    line-height: 3.125rem;
    right: 7.4375rem;
    background-color: var(--C00c155);
    /* font-size:2rem!important; */
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius: 0.25rem;
}

.cancel {
    text-align: center;
    position: absolute;
    top: 86%;
    width: 8.625rem;
    height: 3.125rem;
    line-height: 3.125rem;
    left: 7.4375rem;
    background-color: var(--Cee);
    box-shadow: 0px 4px 4px 0px rgb(0 0 0 / 25%);
    border-radius: 0.25rem;
}</style>