import { createApp } from "vue";
import langvue from "../../plugins/langVue";
import FilterTwo from './FilterTwo.vue'
// 导出函数可通过调用 Confirm() 函数动态创建 XtxConfirm 组件
const filter = (props) => {
    return new Promise((resolve, reject) => { //promise封装，ok执行resolve，no执行reject
        // 创建组件实例
        let dom = createApp(FilterTwo,props).use(langvue).mount(document.createElement("div"));
        // 添加元素
        document.body.appendChild(dom.$el);
        // 重写组件的函数
        dom.onConfirm = (x) => {
            // 点击确定调用
            resolve(x)
            // document.body.removeChild(dom.$el)
            // dom =null
        }
        dom.onCancel = function (err) {
            // 点击取消调用
            reject(err)
            // document.body.removeChild(dom.$el)
            // dom =null
        }
    })
}
export default filter