    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 90vh;
    background-color: transparent;
    font-size: var(--font1_625);
}

.size-box {
    width: 17.625rem;
    height: 3.125rem;
    float: right;
    position: relative;
    right: 3rem;
    top: 0.5rem;
}

.cur-counter-box {
    width: 100%;
}

.filter-dlg {
    width: 37.5rem;
    height: 40.625rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: calc(50% - 25.5rem);
    transform: translateX(-50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    z-index: 41;
    overflow: hidden;
}

.download-filter-dlg {
    width: 37.5rem;
    height: 44.625rem;
    background-color: var(--fff);
    position: absolute;
    left: 50%;
    top: calc(50% - 25.5rem);
    transform: translateX(-50%);
    border-radius: 0.375rem;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    z-index: 41;
    overflow: hidden;
}

