    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 3.2688rem;
    background-color: var(--Cee);
    color: var(--C3B);
}

.title-text {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

