    height: 3.2688rem;
    width: 3.2688rem;
    position: absolute;
    right: 0;
    top: 0;
    padding: auto;
}

.icon-close {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.select-btn {
    float: left;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.cdtn-item {
    height: 4rem;
    width: 100%;
    margin-bottom: 1rem;
    position: relative;
}
.static-curve{
    color: #969696;
}
.first-item {
    margin-top: 1.54375rem;
}

.cdtn-item .is-use-item {
    float: left;
    position: relative;
    left: 3.289rem;
    top: 50%;
    transform: translateY(-50%);
}

.is-nouse-item {
    float: left;
    position: relative;
    left: 3.289rem;
    top: 50%;
    transform: translateY(-50%);
    width: 2rem;
    height: 2rem;
}

.cdtn-item span {
    float: left;
    position: relative;
