}

.counterTwo-cpn {
    width: 100%;
}

.forbit-color {
    color: var(--Cb0) !important;
}

.program-pset-input {
    position: absolute;
    top: 0;
    height: 3.125rem;
    width: 17.825rem;
    text-align: center;
    font-size: 1.125rem;
    right: 3rem;
    border-radius: 0.25rem;
    background-color: var(--fff);
    color: var(--C5A);
    border: 1px solid var(--Cf1);
}

.curve-switch {
    position: absolute;
    top: 0.8vh;
    left: 34vw;
}

.program-cnfrm-btn {
    text-align: center;
    position: absolute;
    top: 0;
    height: 3.125rem;
    line-height: 3.125rem;
    width: 8.625rem;
    right: 8.4375rem;
    background-color: var(--C00c155);
    font-size: var(--font1_5);
    color: var(--fff);
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
    border-radius: 0.25rem;
}

.program-cancel-btn {
    text-align: center;
    position: absolute;
    top: 0;
    height: 3.125rem;
    line-height: 3.125rem;
