<template>
    <div class="chart-main">
        <div id="scichart" class="chart-container"></div>
        <div class="x-label">{{ tab_index == 2 || tab_index == 4 ? "angle" : "time" }}</div>
        <div class="operate-chart-box" :contenteditable="true" @blur="show_chart_tab = false">
            <div :class="{ 'select-chart': true, 'bgc-white': selectStatus == 0 }" @click="clickOperateBox(0)">
                <svg-loader class="chart-icon" size="2.5rem" icon="Chart" />
            </div>
            <div :class="{ 'select-chart': true, 'bgc-white': selectStatus == 3 }" @click="clickOperateBox(3)">
                <svg-loader class="chart-icon" size="2.5rem" icon="AutoChart" />
            </div>
            <div class="chart-options" v-show="show_chart_tab" :contenteditable="false">
                <div :contenteditable="false" :class="{ 'active-chart': tab_index == 2 }" @click="switchChart(2)">
                    {{ $t("curve.torqueAngleCurve") }}
                </div>
                <div :contenteditable="false" :class="{ 'active-chart': tab_index == 1 }" @click="switchChart(1)">
                    {{ $t("curve.timeAngleTorqueCurve") }}
                </div>
                <div :contenteditable="false" :class="{ 'active-chart': tab_index == 3 }" @click="switchChart(3)">
                    {{ $t("curve.speedCurve") }}
                </div>
                <div :contenteditable="false" :class="{ 'active-chart': tab_index == 4 }" @click="switchChart(4)">
                    {{ $t("curve.angTorSlope") }}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import slopecal from "@/dialogs/filterHis/slopecal.js";
import { XYYChart } from "@/components/charts/chart2Y";
import { XYChart } from "@/components/charts/chart";
import { getCurveData } from "@/api/history/history";
export default {
    data() {
        return {
            tab_index: 2,
            is_unmounted: false,
            showCursor: false,
            auto_fit: true,
            curvedata: undefined,
            curve_points_cnt: 0,
            show_chart_tab: false,
            process_cache: {
                tmin: 0,
                tmax: 0.01,
                angle_min: 0,
                angle_max: 0.01,
                torque_min: 0,
                torque_max: 0.01,
                speed_min: 0,
                speed_max: 0.01,
            },
            selectStatus: -1,
            isAutoFit: false,
            isInited: false,
            global_data: null
        };
    },
    props: {
        pdid: Number,
    },
    mounted() {
        this.createChart(this.tab_index);
    },
    activated() {
        if (this.isInited)
            this.draw();
    },
    unmounted() {
        this.is_unmounted = true;
        if (this.chartProxy)
            this.chartProxy.destory();
    },
    methods: {
        floatPrase(a3, a2, a1, a) {
            let intVal = this.intPrase(a3, a2, a1, a);
            let sig = (intVal >> 31) & 0x01 ? -1 : 1;
            let exp = ((intVal >> 23) & 0xff) - 127;
            let mag = intVal & 0x7fffff;
            let mantissa = (1.0 * mag) / 8388608.0;
            var data = sig * (1 + mantissa) * Math.pow(2, exp);
            return data;
        },

        intPrase(a, a1, a2, a3) {
            return (a3 << 24) + (a2 << 16) + a1 * 256 + a;
        },

        UpdateCurveData() {
            if (this.pdid < 1) {
                return;
            }
            getCurveData(this.pdid).then((data) => {
                // console.log(data);
                if (!data || data.byteLength < 5) return;
                let intArray = new Int32Array(data, 0, 4);
                let len = intArray[0];
                if (data.byteLength != 4 + len * 16) {
                    return;
                }
                var f_array = new Float32Array(data, 4);
                this.curvedata = Array.from(f_array.values());
                this.curve_points_cnt = len;
                this.draw();
            });
        },
        draw() {
            let len = this.curve_points_cnt;
            if (this.pdid < 1 || len < 2) {
                if (this.chartProxy) this.chartProxy.clearSeriesData();
                return;
            }
            this.addSeriesData(
                this.curvedata.slice(len * 2, len * 3),
                this.curvedata.slice(len, len * 2),
                this.curvedata.slice(0, len),
                this.curvedata.slice(len * 3, len * 4)
            );
            if (this.auto_fit) this.fit();
        },
        createChart(tabIndex) {
            this.process_cache.len = 0;
            if (this.chartProxy)
                this.chartProxy.destory();
            if (tabIndex == 1) {
                this.chartProxy = new XYYChart("time", "angle", "torque");
                this.chartProxy.createChart("scichart").then(() => {
                    this.isInited = true;
                    this.draw();
                });
            } else if (tabIndex == 2) {
                this.chartProxy = new XYChart("angle", "torque");
                this.chartProxy.createChart("scichart").then(() => {
                    this.isInited = true;
                    this.draw();
                });
            } else if (tabIndex == 3) {
                this.chartProxy = new XYChart("time", "speed");
                this.chartProxy.createChart("scichart").then(() => {
                    this.isInited = true;
                    this.draw();
                });
            } else if (tabIndex == 4) {
                this.chartProxy = new XYYChart("angle", "torque", "slope");
                this.chartProxy.createChart("scichart").then(() => {
                    this.isInited = true;
                    this.chartProxy.addRectEO([0, 0, 0, 0, 0])
                    this.draw();
                });
            }
        },
        show_cursor_method() {
            this.showCursor = !this.showCursor;
            this.chartProxy.showCursor(this.showCursor);
        },
        addSeriesData(times, positions, sensor_vals, speeds) {
            if (this.chartProxy) this.chartProxy.clearSeriesData();
            switch (this.tab_index) {
                case 1:
                    this.chartProxy.addSeriesData(times, positions.map((x) => x * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset), sensor_vals.map((x) => x * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset));
                    break;
                case 2:
                    this.chartProxy.addSeriesData(positions.map((x) => x * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset), sensor_vals.map((x) => x * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset));
                    break;
                case 3:
                    this.chartProxy.addSeriesData(times, speeds.map((x) => x * this.$store.state.sensors['angle_speed'].gain + this.$store.state.sensors['angle_speed'].offset));
                    break;
                case 4:
                    var positions_out = positions.map((x) => x * this.$store.state.sensors['angle'].gain + this.$store.state.sensors['angle'].offset)
                    var sensor_vals_out = sensor_vals.map((x) => x * this.$store.state.sensors['torque'].gain + this.$store.state.sensors['torque'].offset)
                    var GlobalParams = {
                        'angle_sampling': 0.01,
                        'angle_threshold': 0.1,
                        'torque_limit': 1,
                        'k': 3.6
                    }
                    if (this.global_data != null) {
                        GlobalParams = this.global_data
                    }
                    slopecal({ GlobalParams }).then((result) => {
                        this.global_data = result
                        var calPar = {
                            'angle_sampling': result['angle_sampling'] * this.$store.state.sensors['angle'].gain,
                            'angle_threshold': result['angle_threshold'] * this.$store.state.sensors['angle'].gain,
                            'torque_limit': result['torque_limit'] * this.$store.state.sensors['torque'].gain,
                            'k': result['k'] * this.$store.state.sensors['slope'].gain,
                        };
                        var slop = this.getSlopeDate(positions_out, sensor_vals_out, calPar);
                        // console.log(positions_out);
                        // this.chartProxy.addSeriesData(positions_out, sensor_vals_out, times);
                        this.chartProxy.addSeriesData(positions_out, sensor_vals_out, slop.slice(0, -8));
                        // var start_out = slop[slop.length - 4];
                        // var end_out = slop[slop.length - 3];
                        // var temp_k_out = slop[slop.length - 2];
                        // var key = slop[slop.length - 1];
                        // console.log(start_out, end_out, temp_k_out, key);
                        // if (key == 1) {
                        //     this.chartProxy.setRectEO([positions_out[start_out], sensor_vals_out[start_out], positions_out[end_out], sensor_vals_out[end_out], temp_k_out])
                        // } else {
                        //     this.chartProxy.clearRectEO([positions_out[start_out], sensor_vals_out[start_out]]);
                        // }
                        var k_max = parseFloat(slop[slop.length - 8].toFixed(3));
                        var k_max_angle = parseFloat(slop[slop.length - 7].toFixed(3));
                        var k_max_torque = parseFloat(slop[slop.length - 6].toFixed(3));

                        var target_k_out = parseFloat(slop[slop.length - 5].toFixed(3));
                        var target_angle_out = parseFloat(slop[slop.length - 4].toFixed(3));
                        var target_torque_out = parseFloat(slop[slop.length - 3].toFixed(3));
                        var key = slop[slop.length - 2] == 1 ? true : false;

                        var x_scope = positions_out[positions_out.length - 1] - positions_out[0];
                        console.log(k_max, k_max_angle, k_max_torque, target_k_out, target_angle_out, target_torque_out, key, x_scope);
                        this.chartProxy.clearRectEO()
                        this.chartProxy.addRectEO([k_max, k_max_angle, k_max_torque, target_k_out, target_angle_out, target_torque_out, key, x_scope])
                        if (this.auto_fit) this.fit();
                    });
                    break;
                default:
                    break;
            }
        },
        getSlopeDate(positions, sensor_vals, calPar) {
            // 将 JavaScript 数组复制到 WebAssembly 内存中

            let pointNum = positions.length

            //分配内存地址用于跟wsam交互数据，pointNum * 3分别用来存储角度数据、扭矩数据、返回的斜率数据。额外的8代表其他结果数据 ---计算出的最大k，最大k角度、最大k扭矩、目标k，目标k角度、目标k扭矩、是否存在。
            let floatDataNum = pointNum * 3 + 8

            const ptr = window.Module.asm.allocate(floatDataNum);
            let wasmArray = new Float32Array(window.Module.asm.memory.buffer, ptr, floatDataNum);
            wasmArray.set([...positions, ...sensor_vals]);
            let output = new Float32Array(window.Module.asm.memory.buffer, ptr + pointNum * 2 * 4, pointNum + 8);
            // 调用 C 函数
            window.Module.asm.calculate_k_max(ptr, pointNum, calPar['angle_sampling'], calPar['angle_threshold'], calPar['torque_limit'], calPar['k']);
            // window.Module.asm.calculate_k(ptr, pointNum, 100, 3200, 1, 0.00065);
            // 输出结果
            const out = Array.from(output)
            window.Module.asm.deallocate(ptr, floatDataNum)
            return out
            // return Array.from(output)
        },
        clickOperateBox(i) {
            this.show_chart_tab = i == 0;
            if (i == 3) {
                this.fit();
            }
        },
        fit() {
            this.chartProxy.fit();
        },
        last_page() {
            this.tab_index -= 1;
            if (this.tab_index == 0) this.tab_index = 3;
        },
        next_page() {
            this.tab_index += 1;
            if (this.tab_index == 4) this.tab_index = 1;
        },
        legendtext(i) {
            if (i == 1) {
                return "torque";
            }
            if (i == 2) {
                return "speed";
            } else {
                return "angle";
            }
        },
        // 切换曲线
        switchChart(i) {
            this.tab_index = i;
            this.showCursor = false;
            this.show_chart_tab = false;
            if (this.tab_index == 4) this.createChart(this.tab_index);
            // this.chartProxy.showCursor(this.showCursor);
        },
        resetData() {
            this.popvisible = false;
            this.showCursor = false;
            this.auto_fit = true;
            this.selectStatus = -1;
        },
    },
    watch: {
        tab_index(newVal) {
            if (this.tab_index != 4) this.createChart(newVal);
        },
        pdid() {
            this.UpdateCurveData();
        },
    },
    created() {
        window.Module.asm.sayHi();
        this.UpdateCurveData();
        this.$router.beforeEach((to, from, next) => {
            this.tab_index = 2
            next();
        });
    },
};
</script>

<style scoped>
.chart-main {
    position: absolute;

    width: 100%;
    height: 100%;
    --border-storke: #f1f1f1;
}

.operate-chart-box {
    color: transparent;
    outline: none;
    cursor: pointer;
}

.chart-container {
    position: relative;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}

@import "../../res/css/chartbox.css";
</style>
