<template>
    <div id="input_hex" :class="{ 'box': true, 'valid-text': !textValid }">
        <input :class="{ 'hight-light': (hasKybrd && isHightLight && textValid) }" @click="show_keyboard"
            :value="key_value" :type="type" :placeholder="placeholder" @input="valueChanged" onfocus="blur();" />
        <span class="valid-reason" v-show="!textValid">{{ validReason }}</span>
    </div>
</template>

<script>
import showHexkybrd from "@/keyboard/hexkbrd/Hexkeybrd.js"

export default {
    components: {},
    data() {
        return {
            uuid: "hexinput",
            valid: true,
            key_value: '',
            illegalWords: [],
            hasKybrd: false,
        };
    },
    props: {
        text: {
            type: null,
            default: "",
        },
        width: {
            type: String,
            default: "17.625rem",
        },
        pattern: {
            type: String,
            default: "",
        },
        placeholder: {
            type: String,
            default: ''
        },
        ex_style: {
            type: null,
            default: null,
        },
        type: {
            type: String,
            default: 'text'
        },
        fntFmly: {
            type: String,
            default: '--FSY'
        },
        fntSize: {
            type: String,
            default: '1.625rem'
        },
        // 禁用的字符
        imprprChrctr: {
            type: Array,
            default: function () {
                return []
            }
        },
        isShowKeyBoard: {
            type: Boolean,
            default: true
        },
        textValid: {
            type: Boolean,
            default: true,
        },
        validReason: {
            type: String,
            default: "输入不合法"
        },
        isHightLight: {
            type: Boolean,
            default: true,
        },
        isAllowUseHex: {
            type: Boolean,
            default: false
        }

    },
    methods: {
        hexinput(val) {
            this.hanleInput(val);
        },
        changeText(val) {
            this.key_value = val
            this.valid = this.key_value.length == 4;
            if (this.key_value.length != 4)
                this.$emit("update:modelValue", this.key_value.padStart(4, 0));
        },
        show_keyboard(e) {
            if (!this.isShowKeyBoard || this.hasKybrd) return
            this.hasKybrd = true
            this.$emit('focus', true)
            var y = e.clientY - e.offsetY
            let width = 48.5
            let height = 22
            let top = 0
            top = y + e.target.offsetHeight + this.getPct() * height * 16
            if (top >= window.innerHeight) {
                top = y - height * this.getPct() * 16
            } else {
                top = y + e.target.offsetHeight
            }
            showHexkybrd({
                width: width + "rem",
                height: height + "rem",
                top: `${top}px`,
                oldStr: this.key_value,
            }).then((result) => {
                this.key_value = result
                this.$emit("update:modelValue", result);
                this.hasKybrd = false
                this.$emit('focus', false)
            }).catch((err) => {
                this.hasKybrd = false
                this.$emit('focus', false)
                return err
            });
        },
        // 输入框输入时候，将值进行转换
        valueChanged(e) {
            // 初始值为oldCondition.errcode.val
            // console.log(e,'输入框输入时的value');
            this.key_value = e.target.value;
            this.hanleInput(this.key_value);
        },
        // 处理转换后的值
        hanleInput(value) {
            // console.log(value,'转换后的value');
            this.key_value = value;
            this.valid = true;
            // 向父组件传数据,实际传给后台的值(由换算关系和偏移数值组成)和要展示的值，以及合法性
            this.$emit("update:modelValue", value
                //  {
                //     text: value,
                //     valid: this.valid,
                // }
            );
        },
        getPct() {
            let minPct = Math.min(window.innerWidth / 800, window.innerHeight / 1280).toFixed(3)
            return minPct
        },
        initIllegaLWords() {
            this.illegalWords = this.imprprChrctr
        },
    },
    watch: {
        text: {
            deep: true,
            immediate: true,
            handler: function (newVal) {
                this.hanleInput(newVal);
            }
        },
        imprprChrctr: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initIllegaLWords()
            }
        },
    },
    computed: {
        inputStyle() {
            var style = "input";
            if (this.ex_style) {
                style = style + this.ex_style;
            }
            if (this.valid == false) {
                style = style + " warn ";
            }
            return style;
        },
        _fntFmly() {
            var fonts = {
                '--SHSC-M': 'SourceHanSansCN-Medium',
                '--SHSC-R': 'SourceHanSansCN-Regular',
                '--SHSC-B': 'SourceHanSansCN-Bold',
                '--R-R': "Roboto-Regular",
                '--R-M': "Roboto-Medium",
                '--OHSC-R': "ourceHanSansCN-Regular"
            }
            return fonts[this.fntFmly]
        },
        enabled() {
            if(!this.isAllowUseHex)
                return false
            // if (this.module == "NULL")
            //     return true
            // else if (this.module in this.$store.state.f_status.permissions && this.$store.state.f_status.permissions[this.module] == 4)
            //     return true
            else
                return true
        },
        pointerEvent() {
            return this.enabled ? "auto" : "none";
        },
        opacity() {
            return this.enabled ? 1 : 0.5;
        },
    },
    created() {
        this.initIllegaLWords()
    },
};
</script>

<style scoped>
.box {
    height: 3.125rem;
    position: relative;
    width: v-bind(width);
    border-radius: 4px;
    border: 1px solid var(--C65) !important;
    pointer-events: v-bind(pointerEvent);
    opacity: v-bind(opacity);
}

input {
    position: absolute;
    left: 0;
    width: 100%;
    height: 3.125rem;
    font-size: v-bind(fntSize);
    text-align: center;
    border-radius: 4px;
    box-sizing: border-box;
    border: none !important;
    font-family: var(--FROBOT);
    background-color: transparent !important;
    padding: 0 10px;
    font-weight: 500;
    letter-spacing: 0px;
    color: var(--C65) !important;
}

input::-webkit-input-placeholder {
    color: var(--C65) !important;
}

input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: var(--C65) !important;
}

input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: var(--C65) !important;
}

input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: var(--C65) !important;
}

.hight-light {
    border: 2px solid var(--C00c155) !important;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.3);
}

.valid-text {
    color: red !important;
    border: 2px solid red !important;
    box-shadow: -1px 1px 4px red !important;
}

.b0-color {
    color: var(--CB0);
}

.valid-reason {
    display: inline-block;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    top: calc(100% + 1px);
    padding: 1px;
    white-space: nowrap;

    color: red;
    font-size: 1rem;
}
</style>
